import React from 'react'
import { 
    Form,
    Input,
    Select 
} from 'antd';

const { Option } = Select;
const Item = Form.Item;
const formItemLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 16 },
}


export default function Add(props) {
    let{
        handleSelect,
        roles,
        getFieldDecorator,
        updateUser,
        modalFlag
    }=props
    console.log("updateUser===",updateUser);
    return (
        <Form {...formItemLayout}>
            <Item label="">
                {getFieldDecorator('username', {
                    initialValue:
                        ( modalFlag === "update" ? updateUser.username : null),
                    rules: [
                        {required: true,whitespace:true,message: '!' },
                        {max:12,message:"12"},
                        {min:4,message:"4"},
                        {pattern:/^[a-zA-Z0-9]+$/,message:""}
                    ],
                })(
                    <Input
                        placeholder=""
                    />,
                )}
            </Item>
            { modalFlag === "add" ? 
                <Item label="">
                    {getFieldDecorator('password', {
                        initialValue:"",
                        rules: [
                            {required: true,whitespace:true,message: '!' },
                            {max:12,message:"12"},
                            {min:4,message:"4"},
                            {pattern:/^[a-zA-Z0-9]+$/,message:""}
                        ],
                    })(
                        <Input
                            placeholder=""
                        />,
                    )}
                </Item>
            : null }
            <Item label="">
                {getFieldDecorator('email', {
                    initialValue:( modalFlag === "update" ? updateUser["email"] : null),
                    rules: [
                        { required: true, whitespace:true,message: '' },
                        { pattern:/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/, message: '' }
                    ],
                })(
                    <Input
                        type="email"
                        placeholder=""
                    />,
                )}
            </Item>
            <Item label="">
                {getFieldDecorator('phone', {
                    initialValue:( modalFlag === "update" ? updateUser["phone"] : null),
                    rules: [
                        { required: true,whitespace:true, message: '' },
                        { pattern:/^1[3|4|5|8][0-9]\d{4,8}$/, message: '' }

                        
                    ],
                })(
                    <Input
                        placeholder=""
                    />,
                )}
            </Item>
            <Item label="">
                {getFieldDecorator('role_id', {
                    rules: [{ required: true, message: '' }],
                    initialValue:( modalFlag === "update" ? updateUser["role_id"] : "5dda2f901b2c020448427fe6"),
                })(
                    <Select
                        placeholder=""
                        onChange={value =>{handleSelect(value)}}
                    >
                    {roles.map((item,index)=>
                        <Option key={index} value={item._id}>{item.name}</Option>
                    )}
                    </Select>,
                )}
            </Item>
        </Form>
    )
}
