import React, { Component } from 'react'
import UserUI from '../component/user/user';
import {
    reqGetUser,
    reqAddUser,
    reqDeleteUser,
    reqUpdateUser
} from '../api'
import { message,Form } from 'antd';

class User extends Component {
    constructor(props){
        super(props)
        this.state={
            initUserList:[],/**/
            visible:false,/**/
            rolesNames:[],/**/
            roles:[],/**/
            delVisible:false,/**/
            user:null, /**/
            modalFlag:null, /*modal*/
            updateUser:null /**/
        }
    }
    /**/
    initUser = async ()=>{
        const result = await reqGetUser()
        if(result.status === 0){
            const initUserList = result.data.users;
            const roles = result.data.roles;
            const rolesNames = roles.reduce((pre,role)=>{
                pre[role._id] = role.name
                return pre
            },{}) 
            this.setState({
                initUserList,
                rolesNames,
                roles
            })
        }else{
            message.error("")
        }
    }
    /**/
    handleOk = ()=>{
        //
        this.props.form.validateFields( async(err,values)=>{
            const flag = this.state.modalFlag;
            if(!err){
                let result = null;
                const {username,email,phone,role_id} = values;
                if(flag === "add"){
                    const {password} = values;
                    result = await reqAddUser({
                        username,
                        password,
                        email,
                        phone,
                        role_id
                    })
                }else{
                    const {_id} = this.state.updateUser;
                    result = await reqUpdateUser({
                        username,
                        _id,
                        email,
                        phone,
                        role_id
                    })
                }
                if(result.status === 0){
                    message.success(`${flag==="add" ? "" : ""}`)
                    this.props.form.resetFields()
                    this.initUser()
                    this.setState({
                        visible:false
                    })
                }else{
                    message.error(`${flag==="add" ? "" : ":"+result.msg}`)
                }

            }
        })
    }
    /**/
    handleCancel = ()=>{
        this.setState({
            visible:false,
            delVisible:false
        })
    }
    /**/
    addUser = ()=>{
        this.setState({
            visible:true,
            modalFlag:"add"
        })
    }
    /**/
    handleSelect=(value)=>{
        
    }
    /**/
    delUserModal = (user)=>{
        this.setState({
            delVisible:true,
            user
        })
    }
    /**/
    delUser = async ()=>{
        const user = this.state.user;
        const _id = user._id;
        const result = await reqDeleteUser(_id);
        if(result.status === 0){
            message.success("")
            this.initUser()
        }else{
            message.error("")
        }
        this.setState({
            delVisible:false
        })
        
    }
    /**/
    updateUserModal = (updateUser)=>{
        //
        this.setState({
            updateUser,
            visible:true,
            modalFlag:"update"
        })
    }
    componentDidMount() {
        this.initUser()
    }
    
    render() {
        let {
            initUserList,
            visible,
            rolesNames,
            roles,
            delVisible,
            updateUser,
            modalFlag
        }=this.state
        const { getFieldDecorator } = this.props.form;
        return (
            <UserUI 
                initUserList={initUserList}
                visible={visible}
                handleCancel={this.handleCancel}
                handleOk={this.handleOk}
                addUser={this.addUser}
                getFieldDecorator={getFieldDecorator}
                rolesNames={rolesNames}
                roles={roles}
                handleSelect={this.handleSelect}
                delUser={this.delUser}
                delVisible={delVisible}
                delUserModal={this.delUserModal}
                updateUserModal={this.updateUserModal}
                updateUser={updateUser}
                modalFlag={modalFlag}
            />
        )
    }
}
const UserForm = Form.create({})(User);
export default UserForm