import React, { Component } from 'react';
import { connect } from 'react-redux';
import ProductHomeUI from '../component/product/product-home';
import MyButton from '../component/button/button';
import { 
    Button,
    Form,
    message
} from 'antd';
import {reqProducts,reqSearchProducts,reqUpdateStatus} from '../api'
import {getImgsFromDetailAction} from '../actions/actionOperations/action-picture-wall-operation'
import {BASE_IMG_URL} from '../tools/constants'


export class ProductHome extends Component {
    constructor(props){
        super(props)
        this.state={
            title:null, /*jsx*/
            extra:null, /*jsx*/
            products:[], /**/
            total:0, /**/
            data:[],/**/
            loading:false,/**/
            searchType:"productName",/**/
            searchName:"",/**/
            pageNum:""/**/
        }
    }
    /**/
    initColumns = ()=>{
        const columns = [
            {
              title: '',
              dataIndex: 'name',
            },
            {
              title: '',
              dataIndex: 'desc',
            },
            {
                title: '',
                width:120,
                dataIndex: 'price',
                render:(price)=> "" + price
            },
            {
                title: '',
                width:100,
                render:(product)=> {
                    const {status,_id}=product;
                    const newState = status === 1 ? 2 : 1;
                    return(
                        <span>
                            <Button 
                                type="primary" 
                                onClick={()=>{this.updateStatus(_id,newState)}}>
                                {status ===  1? "" : ""}
                            </Button>
                            <span>{status===1 ? "" : ""}</span>
                        </span>
                    )
                }
            },
            {
                title: '',
                width:100,
                render:(product)=> {
                    return(
                        <span>
                            <MyButton onClick={()=>this.props.history.push("/product/detail",product)}></MyButton>
                            <MyButton onClick={()=>{this.changeInfo(product)}}></MyButton>
                        </span>
                    )
                }
            },
          ];

        this.setState({
            products:columns
        })
    }
    /**/
    changeInfo = (product)=>{
        console.log("product===>",product)
        //action
        const {imgs} = product;
        const images = imgs.map((item,index)=>{
           return {
                uid: -index,/*fileid*/
                name: item,/**/
                status: 'done',/* done-*/
                url: BASE_IMG_URL + item,
            }
        })
        this.props.changeImgs(images)
        this.props.history.push("/product/add",product)/**/
    }
    /**/
    updateStatus = async (id,status)=>{
      const result = await reqUpdateStatus(id,status)
      if(result.status === 0){
        message.success("",1);
        this.getProductInfo(this.state.pageNum)
      }
    }
    /**/
    handleKeyWordsInput =(event)=>{
        this.setState({
            searchName:event.target.value
        })
    }
    /**/ 
    handleSelect =(value)=>{
        this.setState({searchType:value})
    }
    // 
    initAddProduct = ()=>{
        return (
            <Button type="primary" icon="plus" onClick={()=>{
                this.props.history.push("/product/add")
            }}>
                
            </Button>
        )
    }
    /**/
    getProductInfo = async(pageNum)=>{
        /**/
        this.setState({
            loading:true,
            pageNum /**/
        })
        let result = null;
        const {searchName,searchType} = this.state;
        if(searchName){
            result = await reqSearchProducts(pageNum,3,searchName,searchType)
        }else{/**/
            result = await reqProducts(pageNum,3)
        }
       this.setState({
            loading:false
        })
       if(result.status === 0){
        const {total,list} = result.data;
        this.setState({
            total,
            data:list
        })
       }
    }
    UNSAFE_componentWillMount(){
        this.initColumns()
        this.setState({
            extra:this.initAddProduct()
        })
    }
    componentDidMount() {
        this.getProductInfo(1)
    }
    
    render() {
        let {
            extra,
            products,
            data,
            total,
            loading,
            searchName,
            searchType,
            pageNum
        }=this.state
        return (
            <ProductHomeUI 
                extra={extra}
                columns={products}
                data={data}
                total={total}
                getProductInfo={this.getProductInfo}
                loading={loading}
                handleKeyWordsInput={this.handleKeyWordsInput}
                searchName={searchName}
                searchType={searchType}
                handleSelect={this.handleSelect}
                pageNum={pageNum}
            />
        )
    }
}

const mapStateToProps = (state) => ({
    
})

const mapDispatchToProps = dispatch =>({
        changeImgs:(imgs)=>{
        dispatch(getImgsFromDetailAction(imgs))
    }
})
const ProductHomeForm = Form.create({})(ProductHome);
export default connect(mapStateToProps, mapDispatchToProps)(ProductHomeForm)
