import React, { Component } from 'react'
import { connect } from 'react-redux'
import RoleUI from '../component/role/role'
import {reqGetRoles,reqAddRole,reqGetAuth} from '../api'
import {message,Form,Tree} from 'antd'
import menuList from '../config/initMenu';
import ramInfo from '../tools/ramInfo';
import localInfo from '../tools/localInfo';

const { TreeNode } = Tree;

export class Role extends Component {
    constructor(props){
        super(props)
        this.state={
            data:[], /*@data array*/
            role:{}, /*role @role object*/
            addVisible:false,/*add @addVisible boolean*/
            authVisible:false, /*auth @authVisible boolean*/
            name:null, /* @name string*/
            authList:[],/* @authList array*/
            menus:[],/* @menus array*/
            checkedKeys:[]/*key @selectedKeys array */
        }
    }
    /**/
    initAuthList = (menuList)=>{
        return menuList.reduce((pre,item)=>{
            pre.push(
                <TreeNode title={item.title} key={item.key} dataRef={item}>
                    {item.children ? this.initAuthList(item.children) : null}
                </TreeNode>                
            )
            return pre
        },[])
    }
    /**/
    onSelectCallback = (role)=>{
        this.setState({
            role
        })
    }
    /**/
    onRow = (role)=>{
        const menus = role.menus
        return {
            onClick:(event)=>{
                console.log(role)
                this.setState({
                    role,
                    menus
                })
            }
        }
    }
    /**/
    getRoles = async ()=>{
        const result  = await reqGetRoles();
        if(result.status === 0){
            const roles = result.data;
            this.setState({
                data:roles
            })
        }else{
            message.error("...")
        }
    }
    /**/
    handleCancel = e => {
        this.props.form.resetFields()
        this.setState({
            addVisible: false,
            authVisible:false
        });
    };
    /*add*/
    handleOk = e => {
        this.props.form.validateFields(async (error,values)=>{
            if(!error){
                const {roleName} = values;
                const result = await reqAddRole(roleName)
                if(result.status === 0){
                    message.success("");
                    this.props.form.resetFields()
                    // this.getRoles();
                    const role = result.data;
                    /**/
                    this.setState((state,props)=>({
                        data:[...state.data,role]
                    }))
                }else{
                    message.error("")
                }
                this.setState({
                    addVisible: false,
                });
            }
        })
    };
    /**/
    showModalTitle= (title)=>{
        if(title === "add"){
            this.setState({
                addVisible:true
            })
        }else{
            const {name} = this.state.role;
            this.setState({
                authVisible:true,
                name
            })
        }
    }
    /**/
    onSelect = (CheckedKeys ,info)=>{
        this.setState({
            menus:CheckedKeys,
        })
    }
    /*auth*/
    authOk = async ()=>{
        //menus
        // this.state.role
        const menus = this.state.menus;
        const {_id,auth_time} = this.state.role;
        const auth_name = ramInfo.user.username;
        const result = await reqGetAuth({_id,menus,auth_time,auth_name});
        if(result.status === 0){
            if(ramInfo.user.role._id === _id){ /*login*/
                ramInfo.user = {};
                localInfo.removeLocalData();
                this.props.history.replace("/login");
                message.success(`,`)
            }else{
                message.success(`${this.state.name}`)
                //
                this.getRoles()                
            }
        }else{
            message.error(`${this.state.name}`)
        }
        this.setState({
            authVisible:false,
        })
    }
    componentDidMount() {
        this.getRoles()
    }
    UNSAFE_componentWillMount(){
        const authList = this.initAuthList(menuList)
        this.setState({
            authList
        })
    }
    componentWillUnmount() {
        this.setState = (state, callback) => {
            return;
        }
    }
    
    render() {
        let {
            data,
            addVisible,
            role,
            authVisible,
            name,
            authList,
            menus
        } = this.state
        const { getFieldDecorator } = this.props.form;
        return (
            <RoleUI 
                onRow={this.onRow}
                data={data}
                getFieldDecorator={getFieldDecorator}
                roleOk={this.handleOk}
                handleCancel={this.handleCancel}
                addVisible={addVisible}
                showModalTitle={this.showModalTitle}
                role={role}
                authVisible={authVisible}
                name={name}
                authOk={this.authOk}
                authList={authList}
                onSelect={this.onSelect}
                checkedKeys={menus}
                onSelectCallback={this.onSelectCallback}
            />
        )
    }
}

const mapStateToProps = (state) => ({
    
})

const mapDispatchToProps = {
    
}
const RoleForm = Form.create({})(Role);
export default connect(mapStateToProps, mapDispatchToProps)(RoleForm)
