import React from 'react'
import {
    Card,
    Input,
    Button,
    Form,
    Icon,
    Cascader,
} from 'antd'
import MyButton from '../button/button';
import PictureWall from '../../containers/picture-wall';
import RichTextEditor from '../rich-text-editor/rich-text-editor';

const {Item} = Form;
const { TextArea } = Input;

/**/
const formItemLayout = {
    labelCol: { span: 2 },
    wrapperCol: { span: 8 },
}

export default function ProductAdd(props) {
    /**/
    let { 
        getFieldDecorator, /**/
        submitAddProduct,/**/
        validatorPrice,/**/
        options,/**/
        loadData,/**/
        goBackToHome,/**/
        editor,/*refs*/
        name,
        desc,
        detail,
        price,
        imgs,
        categoryId,
        pCategoryId,
    } = props;
    /*card*/ 
    const title = (
        <span>
            <MyButton onClick={()=>{goBackToHome()}}>
                <Icon type="arrow-left" style={{fontSize:20}}/>
                <span></span>
            </MyButton>
        </span>
    )
    const categoryIds = [];
    if(pCategoryId === "0"){ //
        categoryIds.push(categoryId);
    }else{
        categoryIds.push(pCategoryId);
        categoryIds.push(categoryId);
    }
    return (
        <Card title={title}>
            <Form {...formItemLayout}>
                <Item  label="">
                    {getFieldDecorator("name",{
                        initialValue:name,
                        rules:[
                            {required:true,message:""}
                        ]
                    })(<Input placeholder=""/>)}
                </Item>
                <Item  label="">
                    {getFieldDecorator("desc",{
                        initialValue:desc,
                        rules:[
                            {required:true,message:""}
                        ]
                    })(<TextArea 
                        placeholder=""
                        autoSize={{ minRows: 2 }}
                    />)}
                </Item>
                <Item  label="">
                    {getFieldDecorator("price",{
                        initialValue:price,
                        rules:[
                            {required:true,message:""},
                            {validator:validatorPrice}
                        ]
                    })(<Input type="number" placeholder="" addonAfter=""/>)}
                </Item>
                <Item  label="">
                {getFieldDecorator("categoryIds",{
                        initialValue:categoryIds,
                        rules:[
                            {required:true,message:""},
                        ]
                    })(<Cascader
                        options={options}
                        loadData={loadData}
                        placeholder=""
                />)}
                </Item>
                <Item  label="">
                   <PictureWall /*ref={pw}*/ imgs={imgs}/>
                </Item>
                <Item  label="" labelCol={{span: 2}} wrapperCol={{span: 18}}>
                   <RichTextEditor ref={editor} detail={detail}/>
                </Item>
                <Item labelCol={{span: 2}}>
                    <Button type="primary" onClick={()=>{submitAddProduct()}}></Button>
                </Item>
            </Form>
        </Card>
    )
}