import React from 'react'
import { Form,Input} from 'antd';

export default function Update(props) {
    return (
        <Form>
            <Form.Item>
            {props.getFieldDecorator('categoryName', {
                initialValue:props.categoryName,
                rules: [{ required: true, message: '' }],
            })(
                <Input
                placeholder=""
                />,
            )}
            </Form.Item>
        </Form>
    )
}
